import React from 'react';
import MenuLink from './MenuLink';

const Menu = (props) => {
	const menuLinks = props.menu.map(item => 
		<MenuLink key={item.id} link={item.link} icon={item.icon} path={item.path} />);
	
	return (
		<nav className="page__menu menu-page">
			<ul className="menu-page__list">
				{menuLinks}
			</ul>
		</nav>
	);
}

export default Menu;