import actions from './actions';
import { authAPI } from '../../api';

export const auth = () => async (dispatch) => {
	return authAPI.getAuth().then(data => {
		if (data.resultCode === 0) {
			dispatch(actions.setAuthUserData(data.user, true));
		}
	});
};

export const login = (username, password) => async (dispatch) => {
	const data = await authAPI.login(username, password);

	if (data.resultCode === 0) {
		dispatch(actions.setAuthUserData(data.user, true));
	} else {
		dispatch(actions.setMessage(data.message));
	}
};

export const register = (username, password) => async (dispatch) => {
	const data = await authAPI.register(username, password);
	
	if (data.resultCode === 0) {
		dispatch(actions.setAuthUserData(data.user, true));
	}
};

export const logout = (username, password) => async (dispatch) => {
	const data = await authAPI.logout(username, password);
	
	if (data.resultCode === 0) {
		dispatch(actions.setAuthUserData(null, false));
	}
};

export default {
	auth,
	login,
	register,
	logout
};
