import * as types from './types';

const initialState = {
	user: {
		id: null,
		username: null
	},
	isAuth: false,
	message: ""
}

const auth = (state = initialState, action) => {
	switch (action.type) {
		case types.SET_AUTH_USER_DATA:
			return {
				...state,
				user: { ...action.user },
				isAuth: action.isAuth
			}
		case types.SET_MESSAGE:
			return {
				...state,
				message: action.message
			}
		default:
			return state;
	}
}


export default auth;