import * as types from './types';

const addPost = (text) => ({
	type: types.ADD_POST,
	text
});

const deletePost = (postId) => ({
	type: types.ADD_POST,
	postId
});


const updateStatus = (newStatus) => ({
	type: types.UPDATE_STATUS,
	newStatus
});

const setUserData = (userData) => ({
	type: types.SET_USER_DATA,
	userData
});

export default {
	addPost,
	deletePost,
	updateStatus,
	setUserData
}