import * as types from './types';
import { combineReducers } from 'redux';

const initialState = [
	{ likesCount: 23, message: 'Классная аватарка! Я на тебя подписан!', id: 1 },
	{ likesCount: 5, message: 'Yo Yo Yo', id: 2 },
	{ likesCount: 34, message: 'Хорошая веб-страница! Мне очень нравится', id: 3 },
	{ likesCount: 120, message: 'Изучению программирование каждый день.', id: 4 },
];

const userData = (state = {}, action) => {
	switch (action.type) {
		case types.SET_USER_DATA:
			return action.userData;
		case types.UPDATE_STATUS:
			return {
				...state,
				status: action.newStatus
			}
		default:
			return state;
	}
};

const posts = (state = initialState, action) => {
	switch (action.type) {
		case types.ADD_POST:
			const nextPostId = ++state.map(post => post.id)[state.length - 1];
			
			return [
				...state,
				{
					likesCount: 0,
					message: action.text,
					id: nextPostId,
				}
			]
		case types.DELETE_POST:
			return {
				...state,
				posts: state.posts.filter(post => post.id !== action.postId)
			}
		default:
			return state;
	}
};



const profile = combineReducers({
	userData,
	posts
});

export default profile;