export const SEND_MESSAGE = "SEND_MESSAGE";
export const UPDATE_NEW_MESSAGE_TEXT = "UPDATE_NEW_MESSAGE_TEXT";
export const SET_USERS = 'SET_USERS';
export const TOGGLE_FOLLOW = 'TOGGLE_FOLLOW';
export const FILTER_USERS = 'FILTER_USERS';
export const UPDATE_USERS_SEARCH = 'UPDATE_USERS_SEARCH';
export const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
export const SET_SELECTED_PAGE = 'SET_SELECTED_PAGE';
export const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
export const SET_AUTH_USER = 'SET_USER_DATA';
export const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS';
// export const SET_MESSAGE = 'SET_MESSAGE';
export const INITIALIZED_SUCCESS = 'INITIALIZED_SUCCESS';


export const sendMessage = (message) => ({
	type: SEND_MESSAGE,
	message
});

export const toggleFollow = (userId) => ({
	type: TOGGLE_FOLLOW,
	userId
});


export const setUsers = (users) => ({
	type: SET_USERS,
	users
});

export const filterUsers = () => ({
	type: FILTER_USERS,
});

export const updateUsersSearch = (searchText) => ({
	type: UPDATE_USERS_SEARCH,
	searchText
});

export const setTotalUsersCount = (totalUsersCount) => ({
	type: SET_TOTAL_USERS_COUNT,
	totalUsersCount
});

export const setSelectedPage = (selectedPage) => ({
	type: SET_SELECTED_PAGE,
	selectedPage
});

export const toggleIsFetching = () => ({
	type: TOGGLE_IS_FETCHING
});


// export const setAuthUserData = (user, isAuth) => ({
