import React, { lazy, Suspense } from 'react';
import { connect } from 'react-redux';
import { appOperations } from '../redux/ducks/app';
import { getInitialized } from '../redux/selectors/appSelectors';

import HeaderContainer from './HeaderContainer';
import { Route } from 'react-router-dom';
import Friends from './pages/Friends';
import MenuContainer from './Menu/MenuContainer';
import Music from './pages/Music';
import Gallery from './pages/Gallery';
// import DialogsContainer from './Dialogs/DialogsContainer';
import ProfileContainer from './Profile/ProfileContainer';
// import UsersContainer from './Users/UsersContainer';
import LoginContainer from './Login/LoginContainer';
import Preloader from './common/Preloader';


const DialogsContainer = lazy(() => import('./Dialogs/DialogsContainer'));
const UsersContainer = lazy(() => import('./Users/UsersContainer'));

class App extends React.Component {
   componentDidMount() {
      this.props.initializeApp();
   }

   render() {
      if (!this.props.initialized) {
         return <Preloader />
      }

      return (
         <div className="wrapper">
            <HeaderContainer />
            <main className="page">
               <div className="page__container _container">
                  <MenuContainer />
                  <div className="page__content content-page">
                     <Route
                        render={() => (
                           <ProfileContainer />
                        )}
                        path="/profile/:userId?"
                     />
                     <Suspense fallback={<div>Загрузка...</div>}>
                        <Route
                           render={() => (
                              <DialogsContainer />
                           )}
                           path="/chat"
                        />
                        <Route
                           render={() => (
                              <UsersContainer />
                           )}
                           path="/friends"
                        />
                     </Suspense>

                     <Route component={Friends} path="/friends" />
                     <Route component={Music} path="/music" />
                     <Route component={Gallery} path="/gallery" />
                     <Route render={() => <LoginContainer />} path="/login" />
                  </div>
               </div>
            </main>
         </div>
      );
   }
};

const mapStateToProps = (state) => ({
   initialized: getInitialized(state)
})

const mapDispatchToProps = {
   initializeApp: appOperations.initializeApp
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
