import React from 'react';
import * as axios from 'axios';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { compose } from 'redux';

import profileOperations from '../../redux/ducks/profile/operations';
import { withAuthRedirect } from '../hoc';
import { profileSelectors } from '../../redux/ducks/profile';
import { fetchingSelectors } from '../../redux/ducks/fetching';
import Profile from './Profile';

class ProfileContainer extends React.Component {
   componentDidMount() {
      this.props.getUserData();
   }
   render() {
      return <Profile {...this.props} />
   }
}
 
const mapStateToProps = (state) => {
   return {
      posts: profileSelectors.getPosts(state),
      newPostText: profileSelectors.getPostText(state),
      userData: profileSelectors.getUserData(state),
      isFetching: fetchingSelectors.getIsFetching(state),
   }
}

const mapDispatchToProps = {
   addPost: profileOperations.addPost,
   getUserData: profileOperations.getUserData,
   updateStatus: profileOperations.updateStatus
};

export default compose(
   connect(mapStateToProps, mapDispatchToProps),
   withRouter,
   withAuthRedirect,
)(ProfileContainer);
