import actions from './actions';
import { usersAPI } from '../../api';
import { fetchingOperations } from '../fetching';

const addPost = actions.addPost;

const getUserData = () => async (dispatch) => {
	dispatch(fetchingOperations.setIsFetching(true));
	
	const data = await usersAPI.getUserData();
	
	dispatch(actions.setUserData(data.userData));
	dispatch(fetchingOperations.setIsFetching(false));
};

const updateStatus = (status) => async (dispatch) => {
	const data = await usersAPI.getStatus(status);
	dispatch(actions.updateStatus(data.status));
}

export default {
	addPost,
	getUserData,
	updateStatus,
}